import React, {Component} from 'react';
import {Provider} from 'react-redux';
import {Col, Layout, Row} from 'antd';
import {store} from '../../redux/store';
import '../../config/i18n';
import LinceVideoTool from '../../components/lince/LinceVideoTool';
import LinceRegister from '../../components/lince/LinceRegister';
import LinceVideoPlayer from '../../components/lince/LinceVideoPlayer';
import StyledResizable from "../../components/lince/StyledResizable";
/*
const style = {
    borderRight: 'solid 5px #ddd',
    margin: '5px 5 5 5',
};*/

class VideoApp extends Component {
    constructor(props) {
        super(props);
        this.state = {
            date: new Date(),
            tool: {}
        };
        this._doRegisterPlay = this._doRegisterPlay.bind(this);
        this._doSave = this._doSave.bind(this);
    }

    _doRegisterPlay(o) {
        /* OJO!!! Importante!!! Dark area!!! => Se debe eliminar el warning terrorifico: forwardref es para hooks
        Falta info.
        El componente usa connect. Por lo que no podemos acceder a la funcion directamente
        usando connect(mapStateToProps, {fetchVideos},null,{ withRef: true })
        genera un metodo adicional denominado getWrappedInstance para invocar metodos del componente
        https://stackoverflow.com/questions/34004331/react-es6-how-to-access-child-component-functions-via-refs
        https://react-redux.js.org/api/connect#forwardref-boolean
         */
        this.refs.innerPlayer.doPlayScene(o.videoTime);
        this.refs.linceTool.loadSceneData(o);

    }

    _doSave() {
        let time = this.refs.innerPlayer.getCurrentTime();
        this.refs.linceTool.remoteSave(time);
        this.refs.innerPlayer._doPlay();
        //TODO no lo encuentra, pq?
        //this.refs.register.clearSelection();
    }

    render() {

        return (
            <Provider store={store}>
                <Layout>
                    <div style={{display: 'flex', width: '100%', height: '100%'}}>
                        <StyledResizable defaultSize={{width: '65%', height: '100%'}}
                                         minWidth="300"
                                         minHeight="600">
                            <Row>
                                <Col span={24}>
                                    <LinceVideoPlayer ref="innerPlayer"/>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={24}>
                                    <LinceRegister ref="register" onPlayRegister={this._doRegisterPlay}/>
                                </Col>
                            </Row>
                        </StyledResizable>
                        <div style={{width: "100%"}}>
                            <LinceVideoTool ref="linceTool" tool={this.state.tool} onSave={this._doSave}/>
                        </div>
                    </div>
                </Layout>
            </Provider>
        );
    }
}

export default VideoApp;